:root {
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColor: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(223, 223, 238);
    --cardBG: rgb(225, 225, 235);
    --cardHover:rgb(189, 190, 219);
}
.main{
    width: 100%;
    /* margin: 500px; */
}
.secTitle{
    margin-top: 575px;
}
.secTitle .title{
position: relative;
width: max-content;
color:var(--textColor);
margin: 1rem 0;
z-index: 2;
}
.secTitle .title:hover{
    position: absolute;
    content: '';
    background: var(--SecondaryColor);
    height: .5px;
    width: 120px;
    right: 0;
    bottom: 0;
    z-index: -1;
}
.sectContent{
    justify-content: center !important;
    gap: 1.5rem;
}
.singleDestination{
    height: 100%;
    display: grid;
    row-gap: 10px;
    /* padding: 1rem; */
    border-radius: 10px;
    align-items: center;
    background: var(--cardBG);
    box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
    overflow: hidden;
}
.singleDestination:hover{
    background: var(--cardHover);
    box-shadow: 1 4px 4px rgba(114, 85, 96, 0.549);
    transition: .3s ease;

}
.imageDiv{
height: 100%;
width: 100%;
overflow: hidden;
}
.imageDiv img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    transition: .2s ease;
}
.imageDiv img:hover{
    transform: scale(1.1);
}
.cardInfo{
    padding: 1rem;

}
.desTitle{
    color:var(--blackColor);
    font-size: 1.25rem;
}
.cardInfo .continent{
    gap: .5rem;

}
.cardInfo .continent .icon{
color:var(--textColor);
font-size: 14px;}
.cardInfo .continent .name{
    color:var(--textColor);
    font-weight: 400;
    font-size: 12px;
    
}
.fees{
    width: 100%;
    justify-content: space-between;
    padding: 1rem;
    margin: 1rem 0;
    border-top: 1.5px solid var(--greyText);
    border-bottom: 1.5px solid var(--greyText);
}
.fees .grade{
    color:var(--textColor);
    max-width: 100px;
    line-height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.fees .grade span small{
    text-align: center;
    background:var(--gradientColor);
    border-radius: 1rem;
    color:var(--whiteColor);
    padding: 1px 12px;
    font-size: 10px;
    margin-left: 4px;

}
.fees .price{
    font-size: 2rem;
    color:var(--blackColor);
}
.desc{
font-size: 13.5px;
color:var(--textColor);
font-weight: 500;
}
.btn{
    margin-top: 1rem;
    color:var(--whiteColor);
    font-weight: 600;
    justify-content: space-between;
    gap: .5rem;

}
.btn .icon{
    align-self: center;
    color:white;
    font-size: 18px;
}
/* //////////////////  media query */
@media screen and (min-width :500px){
    .secTitle{
        margin-top: 740px;
    }
   .secTitle .title{
    font-size: 1.5rem;
    
   }
   .secTitle .title::after{
    width: 155px !important;
   }
   .sectContent{
    grid-template-columns: repeat(2,1fr);
    /* background-color: blue; */
   }
    .singleDestination{
    height: auto;
   }
}
@media screen and (min-width:660px){
    .secTitle{
        margin-top: 590px;
    }
}
@media screen and (min-width:840px){
    .sectContent{
        grid-template-columns: repeat(3,1fr);
       }
       .singleDestination{
        height: auto;
       }
}